.instruction {
  width: 250px;
  height: 100px;
  background-color: rgba(236, 137, 96, 0.8);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 18px;
}

.inputWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.inputLine {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
}